.about {
  .about-content {
    p {
      margin-bottom: 20px;
      font: 400 12px/28px 'Open Sans', sans-serif;
      color: #585858;
    }
  }
}

@media (min-width: 768px) {
  .about {
    .about-content {
      p {
        margin-bottom: 30px;
        font-size: 14px;
      }
    }
  }
}